import Component from './Component';
import { getElement } from '@lib/helper';

export default class TodoInput extends Component {
  template() {
    return `
      <input type="text" class="header__input" placeholder="What needs to be done?">
    `;
  }

  mounted() {
    getElement('.header__input', this.el).focus();
  }

  setEvent() {
    const { addItem } = this.props;

    this.addEvent('keypress', '.header__input', (event) => {
      const { key, target } = event;
      const value = target.value.trimStart();
      if (key !== 'Enter' || value === '') {
        return;
      }

      addItem(value);
    });
  }
}
