import Component from './Component';
import { capitalizeFirstLetter } from '@lib/helper';

const STATUS_LIST = ['all', 'active', 'completed'];

export default class TodoFilter extends Component {
  template() {
    const { viewType, activeCount, completedCount } = this.props;
    const getActiveClass = (type) => (type === viewType ? ' footer__tab--active' : '');
    return `
      <div class="footer__items-left">${activeCount} item${activeCount > 1 ? 's' : ''} left</div>
      <div class="footer__tabs">
        ${STATUS_LIST.map(
          (status) =>
            `<div data-status="${status}" class="footer__tab${getActiveClass(
              status
            )}">${capitalizeFirstLetter(status)}</div>`
        ).join('')}
      </div>
      <div class="footer__clear-btn">Clear Completed (${completedCount})</div>
    `;
  }

  setEvent() {
    const { updateViewType, removeCompletedItem } = this.props;

    this.addEvent('click', '.footer__tab', ({ target }) => {
      updateViewType(target.dataset.status);
    });

    this.addEvent('click', '.footer__clear-btn', () => {
      removeCompletedItem();
    });
  }
}
