const getElement = (selector, parent) => {
  let element = null;
  if (isString(selector)) {
    element =
      parent && isElement(parent)
        ? parent.querySelector(selector)
        : document.querySelector(selector);
  } else if (isElement(selector)) {
    element = selector;
  }

  if (isNull(element)) {
    throw new Error('does not exist element');
  }

  return element;
};

const getElements = (selector, parent) => {
  let element = [];
  if (isString(selector)) {
    element =
      parent && isElement(parent)
        ? parent.querySelectorAll(selector)
        : document.querySelectorAll(selector);
  } else if (isElement(selector)) {
    element = [selector];
  }

  return element;
};

const isNull = (v) => v === null;
const isString = (v) => typeof v === 'string';
const isElement = (v) => v instanceof Element;
const isObject = (v) => Object.prototype.toString.call(v) === '[object Object]';
const capitalizeFirstLetter = (string) => {
  let result;
  if (!isString(string)) {
    result = string;
  } else {
    result = string.charAt(0).toUpperCase() + string.slice(1);
  }
  return result;
};
const getUniqueId = () => {
  return Date.now().toString(36) + Math.random().toString(36).substring(2);
};

export {
  getElement,
  getElements,
  getUniqueId,
  isString,
  isElement,
  isNull,
  isObject,
  capitalizeFirstLetter,
};
