import { isObject, isElement } from './helper';

const listener = new Map();

const getRegisteredEvents = (element) => {
  let registeredEvents = listener.get(element);

  if (!registeredEvents) {
    registeredEvents = listener.set(element, new Map()).get(element);
  }

  return registeredEvents;
};

const getHandlers = (element, eventType) => {
  const registeredEvents = getRegisteredEvents(element);
  let handlers = registeredEvents.get(eventType);

  if (!handlers) {
    handlers = registeredEvents.set(eventType, []).get(eventType);
  }

  return handlers;
};

const setRegisteredEvents = (element, eventType, newHandlers) => {
  const registeredEvents = getRegisteredEvents(element);
  registeredEvents.set(eventType, newHandlers);
};

const on = (element, eventType, handler) => {
  const handlers = getHandlers(element, eventType);
  handlers.push(handler);
};

const emit = (element, eventType, eventData) => {
  const handlers = getHandlers(element, eventType);
  handlers.forEach((handler) => handler(eventData));
};

const off = (element, eventType, handler) => {
  const handlers = getHandlers(element, eventType);
  setRegisteredEvents(
    element,
    eventType,
    handlers.filter((fn) => fn !== handler)
  );
};

const mixin = (element) => {
  if (!isElement(element) && !isObject(element)) {
    throw new Error('mixin은 Element 또는 Object 에만 적용 가능합니다.');
  }
  element.on = (eventType, handler) => on(element, eventType, handler);
  element.off = (eventType, handler) => off(element, eventType, handler);
  element.emit = (eventType, handler) => emit(element, eventType, handler);
};

export { on, off, emit, mixin };
