import { getElement, getElements } from './helper';
import { mixin } from './custom-event';

export default class DragAndDrop {
  constructor() {
    this.isSetDropzone = false;
    this.guideElement = null;
    this.guidelineElement = null;
    this.draggedElement = null;
    this.mappedCustomDataBySelector = new Map();
  }

  draggable(selector, customData) {
    const elements = getElements(selector);
    const checkDropzone = (element) => !!element.getAttribute('dropzone');

    if (!this.mappedCustomDataBySelector.has(selector)) {
      this.mappedCustomDataBySelector.set(selector, customData);
    }

    elements.forEach((element) => {
      if (checkDropzone(element)) {
        throw new Error('dropzone 요소는 draggable 요소로 등록할 수 없습니다.');
      }

      element.setAttribute('dnd-draggable', true);
      element.addEventListener('mousedown', this.onMouseDown.bind(this));
    });
  }

  dropzone(selector) {
    if (this.isSetDropzone) {
      throw new Error('이미 dropzone 이 등록 되어 있습니다.');
    }

    const element = getElement(selector);

    element.setAttribute('dropzone', true);
    mixin(element);

    this.isSetDropzone = true;

    return element;
  }

  getDropzoneElement(x, y) {
    const belowElements = document.elementsFromPoint(x, y);
    return belowElements.find((element) => !!element.getAttribute('dropzone'));
  }

  getDraggableElement(x, y) {
    const belowElements = document.elementsFromPoint(x, y);
    return belowElements.find(
      (element) =>
        !!element.getAttribute('dnd-draggable') && !element.classList.contains('guide-item')
    );
  }

  getCustomData(element) {
    const classList = Array.from(element.classList);
    return this.mappedCustomDataBySelector.get(
      classList.find((className) => this.mappedCustomDataBySelector.has(className))
    );
  }

  onMouseDown({ pageX, pageY, currentTarget, target }) {
    if (target.tagName === 'INPUT') {
      return;
    }

    const handleKeyUp = ({ key }) => {
      if (key === 'Escape') {
        document.removeEventListener('keyup', handleKeyUp);
        document.removeEventListener('mousemove', handleMouseMove);
        document.removeEventListener('mouseup', handleMouseUp);

        this.clear();
      }
    };
    const handleMouseMove = (event) => {
      this.onMouseMove(event);
    };
    const handleMouseUp = (event) => {
      document.removeEventListener('keyup', handleKeyUp);
      document.removeEventListener('mousemove', handleMouseMove);
      document.removeEventListener('mouseup', handleMouseUp);
      this.onMouseUp(event);
    };

    const guideElement = currentTarget.cloneNode(true);
    const guidelineElement = document.createElement('div');

    guidelineElement.classList.add('guideline');
    guideElement.classList.add('guide-item');

    guideElement.style.width = `${currentTarget.clientWidth}px`;
    guideElement.style.top = `${pageY}px`;
    guideElement.style.left = `${pageX}px`;

    document.body.append(guidelineElement);
    document.body.append(guideElement);

    this.guideElement = guideElement;
    this.guidelineElement = guidelineElement;
    this.draggedElement = currentTarget;

    document.addEventListener('keyup', handleKeyUp);
    document.addEventListener('mousemove', handleMouseMove);
    document.addEventListener('mouseup', handleMouseUp);
  }

  onMouseMove({ pageX, pageY }) {
    const guideElement = this.guideElement;
    const guidelineElement = this.guidelineElement;

    guideElement.style.top = `${pageY}px`;
    guideElement.style.left = `${pageX}px`;

    const draggableEl = this.getDraggableElement(pageX, pageY);
    if (draggableEl) {
      const { top, left, width } = draggableEl.getBoundingClientRect();
      guidelineElement.style.width = `${width}px`;
      guidelineElement.style.top = `${top}px`;
      guidelineElement.style.left = `${left}px`;
      guidelineElement.classList.toggle('show', true);
    } else {
      guidelineElement.classList.toggle('show', false);
    }
  }

  onMouseUp({ pageX, pageY }) {
    const draggableEl = this.getDraggableElement(pageX, pageY);
    const dropzoneEl = this.getDropzoneElement(pageX, pageY);
    if (!dropzoneEl) {
      this.clear();
      return;
    }

    dropzoneEl.emit('drop', {
      srcElement: this.draggedElement,
      destElement: draggableEl,
      customData: this.getCustomData(draggableEl),
    });

    this.clear();
  }

  clear() {
    document.body.removeChild(this.guidelineElement);
    document.body.removeChild(this.guideElement);

    this.guideElement = null;
    this.guidelineElement = null;
    this.draggedElement = null;
  }
}
