import { getElements } from '@lib/helper';

export default class Component {
  _el = null;
  _props = {};
  _state = {};

  constructor(options = {}) {
    const { el, ...props } = options;
    this._el = el;
    this._props = props;
    this.setup();
  }

  setup() {}

  setEvent() {}

  mounted() {}

  template() {
    return '';
  }

  render() {
    if (this._el) {
      this._el.innerHTML = this.template();
      this.setEvent();
      this.mounted();
    }
  }

  get el() {
    return this._el;
  }

  get props() {
    return this._props;
  }

  setState(newState) {
    this._state = {
      ...this._state,
      ...newState,
    };
    this.render();
  }

  addEvent(eventType, selector, handler) {
    const children = [...getElements(selector, this.el)];
    const isTarget = (target) => children.includes(target);
    this.el.addEventListener(eventType, (event) => {
      if (!isTarget(event.target)) {
        return;
      }
      handler(event);
    });
  }
}
