import Component from './Component';
export default class TodoItem extends Component {
  template() {
    const { id, title, completed } = this.props;
    return `
      <li class="main__todo-item ${completed ? 'main__todo-item--completed' : ''}">
        <input type="checkbox" class="main__todo-item-status" data-id="${id}" ${
      completed ? 'checked' : ''
    }>
        <label class="main__todo-item-title">${title}</label>
      </li>
    `;
  }
}
